  color: #1f1f1f;
  margin: 0;
}

h1 {
  font-size: 24px;
}

h2 {
  font-size: 22px;
}

h3 {
  font-size: 18px;
}

h4 {
  font-size: 16px;
}

h5 {
  font-size: 14px;
}

h6 {
  font-size: 13px;
}

#title-h5 {
  font-size: 1.25rem;
  margin-bottom: 0;
  line-height: 1.5;
  padding: 0;
  margin: 0;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: #212121;
  text-decoration: none !important;
  opacity: 1;
}

h1 a:hover,
h2 a:hover,
h3 a:hover,
h4 a:hover,
h5 a:hover,
h6 a:hover {
  opacity: 0.8;
}

a {
  color: #1f1f1f;
  text-decoration: none;
  outline: none;
}

a,
.btn {
  text-decoration: none !important;
  outline: none !important;
  transition: all 0.3s ease-in-out;
}

.btn-custom {
  margin-top: 20px;
  background-color: transparent !important;
  border: 2px solid #ddd;
  padding: 12px 40px;
  font-size: 16px;
}

.lead {
  font-size: 18px;
  line-height: 30px;
  color: #767676;
  margin: 0;
  padding: 0;
}

#navbar {
  border-radius: -1px;
}
blockquote {
  margin: 20px 0 20px;
  padding: 30px;
}

ul,
li,
ol {
  list-style: none;
  margin: 0px;
  padding: 0px;
  margin-right: 0.5em;
}
button:focus {
  outline: none;
}

.form-control::-moz-placeholder {
  color: #2a2a2a;
  opacity: 1;
}

/*------------------------------------------------------------------
    HEADER
-------------------------------------------------------------------*/
.top-navbar {
  position: relative;
  z-index: 10;
}

.top-navbar .navbar {
  padding: 15px 0px;
  position: fixed;
  width: 100%;
  border-radius: 0px 0px 100px 100px;
  transition: height 0.3s ease-out, background 0.3s ease-out,
    box-shadow 0.3s ease-out;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  box-shadow: 0 8px 6px -6px rgba(0, 0, 0, 0.4);
  z-index: 100;
}

.top-navbar .navbar-light .navbar-nav .nav-link {
  color: #010101;
  font-size: 16px;
  padding: 10px 20px;
  border-radius: 2px;
  text-transform: uppercase;
}

.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
  color: #fff;
  background-color: #d65106;
}

.navbar-light .navbar-nav .nav-link.active,
.navbar-light .navbar-nav .show > .nav-link {
  color: white;
}

.top-navbar .navbar-light .navbar-nav .nav-item .nav-link:hover {
  background: #d65106;
  color: #ffffff;
}
.top-navbar .navbar-light .navbar-nav .nav-item.active .nav-link {
  background: #d65106;
  color: #ffffff;
  border-radius: 2px;
}

.nav-item.logo img {
  height: 48px;
  padding: 10px 20px;
}

.nav-item.logo img:hover {
  background: #d65106;
  border-radius: 2px;
}

.nav-item.logo.active img {
  background: #d65106;
  border-radius: 2px;
}

.navbar-expand-lg .navbar-nav .dropdown-menu {
  border: none;
  border-radius: 0px;
  padding: 10px;
  box-shadow: 2px 5px 6px rgba(0, 0, 0, 0.5);
}
.navbar-expand-lg .navbar-nav .dropdown-menu a {
  padding: 10px 10px;
  text-transform: uppercase;
}

.navbar-expand-lg .navbar-nav .dropdown-menu a.dropdown-item:hover {
  background: #d65106;
  color: #ffffff;
}
.navbar-light .navbar-toggler:hover {
  background: #d65106;
}

