.our-team {
  text-align: center;
  transition: all 0.5s ease 0s;
  margin-bottom: 30px;
}
.our-team:hover {
  box-shadow: 0 15px 10px -10px rgba(0, 0, 0, 0.5), 0 1px 4px rgba(0, 0, 0, 0.3),
    0 0 40px rgba(0, 0, 0, 0.1) inset;
}
.our-team .pic {
  overflow: hidden;
  position: relative;
}
.our-team .pic:before,
.our-team .pic:after {
  content: "";
  width: 200%;
  height: 80%;
  background: rgba(38, 37, 37, 0.8);
  position: absolute;
  top: -100%;
  left: -4%;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  transition: all 0.5s ease 0s;
}
.our-team .pic:after {
  background: rgba(214, 81, 6, 0.8);
  top: auto;
  left: auto;
  bottom: -100%;
  right: -4%;
}
.our-team:hover .pic:before {
  top: 0;
}
.our-team:hover .pic:after {
  bottom: 0;
}
.our-team .pic img {
  width: 100%;
  height: auto;
}
.our-team .social {
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  position: absolute;
  bottom: 45%;
  left: 0;
  opacity: 0;
  z-index: 2;
  transition: all 0.5s ease 0.3s;
}
.our-team:hover .social {
  opacity: 1;
}
.our-team .social li {
  display: inline-block;
}
.our-team .social li a {
  display: block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  font-size: 20px;
  color: #fff;
  margin-right: 10px;
  position: relative;
  transition: all 0.3s ease 0s;
}
.our-team .social li a:after {
  content: "";
  width: 100%;
  height: 100%;
  background: #d65106;
  border-radius: 0 20px 20px 20px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: all 0.3s ease 0s;
}
.our-team .social li a:hover:after {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.our-team .team-content {
  padding: 20px;
}
.our-team .title {
  font-size: 22px;
  font-weight: 700;
  letter-spacing: 2px;
  color: #d65106;
  text-transform: uppercase;
  margin-bottom: 7px;
}
.our-team .post {
  display: block;
  font-size: 17px;
  font-weight: 600;
  color: #333333;
  text-transform: capitalize;
}
@media only screen and (max-width: 990px) {
  .our-team {
    margin-bottom: 30px;
  }
}

/* Our team - 2 */

.our-team-two {
  border: 5px solid #d65106;
  border-radius: 0px;
  text-align: center;
  margin: 10px;
  z-index: 1;
  position: relative;
}
.our-team-two:before,
.our-team-two:after {
  content: "";
  width: 100%;
  height: 108%;
  background: rgb(77, 75, 75);
  position: absolute;
  top: 50%;
  left: 0;
  z-index: -1;
  -webkit-transform: translateY(-50%) scaleX(0.3);
          transform: translateY(-50%) scaleX(0.3);
  transition: all 0.3s ease 0s;
}
.our-team-two:after {
  width: 106%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) scaleY(0.25);
          transform: translate(-50%, -50%) scaleY(0.25);
}
.our-team-two:hover:before {
  -webkit-transform: translateY(-50%) scaleX(0.7);
          transform: translateY(-50%) scaleX(0.7);
}
.our-team-two:hover:after {
  -webkit-transform: translate(-50%, -50%) scaleY(0.7);
          transform: translate(-50%, -50%) scaleY(0.7);
}
.our-team-two img {
  width: 100%;
  height: auto;
  border-radius: 0px;
  transition: all 0.3s ease 0s;
}
.our-team-two .team-content {
  width: 93%;
  padding: 25px 0 10px;
  background: #d65106;
  position: absolute;
  bottom: 50px;
  left: 50%;
  opacity: 0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  transition: all 0.3s cubic-bezier(0.5, 0.2, 0.1, 0.9);
}
.our-team-two:hover .team-content {
  bottom: 10px;
  opacity: 1;
}
.our-team-two .title {
  font-size: 25px;
  font-weight: 600;
  color: #fff;
  letter-spacing: 1px;
  text-transform: capitalize;
  margin: 0;
}
.our-team-two .post {
  display: block;
  font-size: 12px;
  color: #fff;
  text-transform: uppercase;
  margin-bottom: 10px;
}
.our-team-two.social {
  padding: 0;
  margin: 0;
  list-style: none;
}
.our-team-two .social li {
  display: inline-block;
  margin: 0 5px;
}
.our-team-two .social li a {
  display: block;
  width: 100px;
  height: 35px;
  line-height: 35px;
  border-radius: 20px;
  background: #fff;
  font-size: 20px;
  color: #d0a772;
  transition: all 0.3s ease 0s;
}
.our-team-two .social li a:hover {
  background: #ffffff;
  box-shadow: 0 0 0 5px rgba(255, 255, 255, 0.3);
  color: #d0a772;
}
@media only screen and (max-width: 990px) {
  .our-team-two {
    margin-bottom: 30px;
  }
}

