#slides .scrollable {
  *zoom: 1;
  position: relative;
  top: 0;
  left: 0;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  height: 100%;
}
#slides .scrollable:after {
  content: "";
  display: table;
  clear: both;
}

.slides-navigation {
  margin: 0 auto;
  position: absolute;
  z-index: 3;
  top: 46%;
  width: 100%;
}
.slides-navigation a {
  position: absolute;
  display: block;
}
.slides-navigation a.prev {
  left: 0;
}
.slides-navigation a.next {
  right: 0;
}

.slides-pagination {
  position: absolute;
  z-index: 3;
  bottom: 0;
  text-align: center;
  width: 100%;
}
.slides-pagination a {
  border: 2px solid #222;
  border-radius: 15px;
  width: 12px;
  height: 25px;
  display: -moz-inline-stack;
  display: inline-block;
  vertical-align: middle;
  *vertical-align: auto;
  zoom: 1;
  *display: inline;
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR4nGP6zwAAAgcBApocMXEAAAAASUVORK5CYII=");
  margin: 2px;
  margin-right: 1em;
  overflow: hidden;
  text-indent: -99999px;
  margin-bottom: 2em;
}

.slides-pagination a.current {
  background: #222;
}

.carousel-control-next,
.carousel-control-prev {
  background: #d65106;
  position: absolute;
  height: 50px;
  width: 50px;
  top: 50%;
  font-size: 20px;
  display: block;
  color: #fff;
  border-radius: 4px;
  line-height: 63px;
  text-align: center;
  transition: all 0.3s ease-in-out;
}

.carousel-control-next-icon,
.carousel-control-prev-icon {
  display: inline-block;
  width: 2rem;
  height: 2rem;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: 100% 100%;
  margin-right: 0.4em;
  margin-top: 0.4em;
}

.carousel-indicators button {
  border: 2px solid #ffffff;
  border-radius: 50%;
  width: 50px;
  height: 20px;
}

.carousel-indicators button.active {
  background: #d65106;
  border: 2px solid #d65106;
}

.carousel-indicators [data-bs-target] {
  width: 20px;
  height: 20px;
}

.carousel-item img {
  width: 100%;
  height: 100vh;
}
.carousel-caption {
  text-align: left;
  top: 45%;
}

@supports (object-fit: cover) {
  .carousel-item img {
    object-fit: cover;
    object-position: center center;
  }
}

