}

.menu .menu-text h3 {
  position: relative;
  display: block;
  font-size: 22px;
  font-weight: 700;
}

.menu .menu-text h3::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 0;
  top: 13px;
  left: 0;
  border-top: 2px dotted #cccccc;
  z-index: -1;
}

.menu .menu-text h3 span {
  display: inline-block;
  float: left;
  padding-right: 5px;
  background: #ffffff;
}

.menu .menu-text h3 strong {
  display: inline-block;
  float: right;
  padding-left: 5px;
  color: #fbaf32;
  background: #ffffff;
}

.menu .menu-text p {
  position: relative;
  margin: 5px 0 0 0;
  float: left;
  display: block;
}

@media (max-width: 575.98px) {
  .menu .menu-text p,
  .menu .menu-text h3,
  .menu .menu-text h3 span,
  .menu .menu-text h3 strong {
    display: block;
    float: none;
    padding: 0;
    margin: 0;
  }

  .menu .menu-text h3 {
    font-size: 18px;
    margin-bottom: 0;
  }

  .menu .menu-text h3 span {
    margin-bottom: 3px;
  }

  .menu .menu-text h3::after {
    display: none;
  }
}

