.customer-reviews-box {
  padding: 70px 0px;
  padding-top: 20px;
}

.carousel-inner .carousel-item .img-box {
  width: 135px;
  height: 135px;
}

.text-warning {
  color: #d65106 !important;
  font-size: 24px;
}

.carousel-item2 img {
  width: 100% !important;
  height: 100% !important;
}

@media (min-width: 320px) and (max-width: 640px) {
  .carousel-inner .carousel-item p {
    font-size: 14px;
  }
  .carousel-control-prev {
    left: -5px;
  }
  .carousel-control-next {
    right: -5px;
  }
}

#reviews .carousel-control-prev {
  background: #d65106;
  color: #ffffff;
  display: block;
  position: absolute;
  width: 8%;
  height: 50px;
  top: 50%;
  border-radius: 4px;
  font-size: 28px;
  opacity: 1;
}
#reviews .carousel-control-prev:hover {
  background: #010101;
  color: #ffffff;
}

#reviews .carousel-control-next {
  background: #d65106;
  color: #ffffff;
  display: block;
  position: absolute;
  width: 8%;
  height: 50px;
  top: 50%;
  border-radius: 4px;
  font-size: 28px;
  opacity: 1;
}
#reviews .carousel-control-next:hover {
  background: #010101;
  color: #ffffff;
}
